@import url("https://fonts.googleapis.com/css2?family=Roboto:wght@400;500&display=swap");
@import url("https://fonts.googleapis.com/css2?family=Inter:wght@400;500;600&display=swap");
body {
    margin: 0;
    line-height: normal;
}
.jdiv,
.jforminput {
    align-self: stretch;
}
.jdiv {
    position: relative;
    font-weight: 500;

}
.jdiv1 {
    position: relative;
    font-weight: 500;
    font-size: 15px;
    color: red;
    top: 30.5rem;
    text-align: center;
}
.jforminput {
    border: 0;
    font-family: Roboto;
    font-size: 1rem;
    background-color: #eefaea;
    border-radius: 6px;
    overflow: hidden;
    display: flex;
    flex-direction: row;
    padding: 1rem 0.75rem;
    align-items: center;
    justify-content: flex-start;
}
.jforminput:focus {
    outline: 0;
}
.ujparent {
    align-self: stretch;
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    justify-content: flex-start;
    gap: 0.35rem;
}
.jforminput1 {
    border: 0;
    font-family: Roboto;
    font-size: 1rem;
    background-color: #eefaea;
    border-radius: 6px;
    overflow: hidden;
    flex-direction: row;
    padding: 1rem 0.75rem;
    align-items: center;
    outline: 0;
}
.jforminput1,
.jforminput5,
.frame-group,
.jparent1 {
    align-self: stretch;
    display: flex;
    justify-content: flex-start;
}
.frame-group {
    height: 17.06rem;
    flex-direction: column;
    align-items: flex-start;
    gap: 1.13rem;
}
.jforminput5,
.jparent1 {
    font-family: Roboto;
}
.jparent1 {
    flex-direction: column;
    align-items: flex-start;
    gap: 0.38rem;
}
.jforminput5 {
    border: 0;
    font-size: 1rem;
    background-color: #eefaea;
    border-radius: 6px;
    height: 3.81rem;
    overflow: hidden;
    flex-shrink: 0;
    flex-direction: row;
    padding: 1rem 0.75rem;
    box-sizing: border-box;
    align-items: center;
    outline: 0;
}
.uframe-container,
.uframe-parent,
.jparent2 {
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
}
.jparent2 {
    align-self: stretch;
    height: 5.56rem;
    align-items: flex-start;
    gap: 0.38rem;
}
.uframe-container,
.uframe-parent {
    gap: 1.13rem;
}
.uframe-container {
    align-self: stretch;
    height: 17.63rem;
    padding: 0 0 5.19rem;
    box-sizing: border-box;
    align-items: flex-start;
}
.uframe-parent {
    position: absolute;
    width: calc(100% - 34px);
    top: 10.69rem;
    right: 1.06rem;
    left: 1.06rem;
    align-items: center;
}
.updatejdiv7,
.joinupdateform-child {
    position: absolute;
    top: 35.5rem;
    height: 2.5rem;
}
.joinupdateform-child {
    width: calc(100% - 34px);
    right: 1.06rem;
    left: 1.06rem;
    border-radius: 2px;
    background-color: #48685e;
}
.updatejdiv7 {
    left: calc(50% - 62px);
    font-size: 0.94rem;
    font-weight: 600;
    text-align: center;
    display: flex;
    align-items: center;
    justify-content: center;
    width: 7.69rem;
}

.jcheckbox-icon {
    position: absolute;
    top: -0.12rem;
    left: -0.31rem;
    border-radius: 5px;
    width: 1.88rem;
    height: 1.88rem;
    cursor: pointer;
}
.jdiv17 {
    position: absolute;
    top: 0.13rem;
    left: -2rem;
    line-height: 1.25rem;
    width: 1.5rem;
}
.jdiv31,
.jradio9 {
    position: relative;
}

.jdiv31 {
    line-height: 1.25rem;
}
.juradio-button-setonon {
    position: absolute;
    top: calc(50% + 116.5px);
    left: calc(50% - 163px);
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: flex-start;
    gap: 0.38rem;
}

.juradio-button-setoffon,
.juradio-button-setoffon1 {
    position: absolute;
    top: calc(50% + 116.5px);
    left: calc(50% + 62.5px);
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: flex-start;
    gap: 0.38rem;
    cursor: pointer;
}
.juradio-button-setoffon1 {
    left: calc(50% - 46px);
}

.ujoinform {
    position: relative;
    background-color: #F8F5F0;
    width: 100%;
    height: 51.31rem;
    overflow: hidden;
    text-align: left;
    font-size: 1.13rem;
    color: #292d32;
    font-family: Inter;
}
@media screen and (max-width: 960px) {
    .div7 {
        font-size: 0.88rem;
    }
}
