@import url("https://fonts.googleapis.com/css2?family=Inter:wght@300;500;600&display=swap");
@import url("https://fonts.googleapis.com/css2?family=Zen+Dots:wght@400&display=swap");
body {
    margin: 0;
    line-height: normal;
}

.ULdiv {
    flex: 1;
    position: relative;
    letter-spacing: -0.01em;
    font-weight: 500;
}
.uflframe,
.uFLframe {
    position: absolute;
    border-radius: 2px;
    overflow: hidden;
    display: flex;
    flex-direction: row;
    padding: 0.63rem 0.5rem;
    box-sizing: border-box;
    align-items: center;
    justify-content: center;
}
.bflframe,
.bFLframe {
    position: absolute;
    border-radius: 2px;
    overflow: hidden;
    display: flex;
    flex-direction: row;
    padding: 0.63rem 0.5rem;
    box-sizing: border-box;
    align-items: center;
    justify-content: center;
}
.uflframe {
    width: 50%;
    top: -0.06rem;
    right: 11.19rem;
    left: -0.06rem;
    background-color: #ebebf0;
    border: 1px solid transparent;
    height: 3.25rem;
}
.uFLframe {
    width: 50%;
    top: 0;
    right: 0;
    background-color: #fff;
    height: 3.13rem;
}
.bflframe {
    width: 50%;
    top: -0.06rem;
    right: 11.19rem;
    left: -0.06rem;
    background-color: #fff;
    border: 1px solid transparent;
    height: 3.25rem;
}
.bFLframe {
    width: 50%;
    top: 0;
    right: 0;
    background-color: #ebebf0;
    height: 3.13rem;
}
.ULtab {
    width: 100%;
    right: 0;
    left: 0;
}
.ULtab {
    position: absolute;
    top: 10.19rem;
    height: 3.13rem;
    text-align: center;
    font-size: 0.94rem;
}

.ULdiv2 {
    position: absolute;
    top: 0;
}
.ULdiv2 {
    left: 0;
    letter-spacing: -0.01em;
    background: linear-gradient(202.17deg, #03c75a, #85c644);
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
}

.ulist-child {
    position: absolute;
    width: calc(100% + 1px);
    top: 5.22rem;
    right: -0.03rem;
    left: -0.03rem;
    border-top: 1px solid #b9b9b9;
    box-sizing: border-box;
    height: 0.06rem;
}
.ULphoto-icon {
    position: relative;
    width: 3.69rem;
    height: 3.63rem;
    object-fit: cover;
}
.ULp,
.ULp1 {
    margin: 0;
    font-weight: 600;
}
.ULp1 {
    font-weight: 300;
    color: #636363;
}
.ULtxt {
    line-break: anywhere;
    width: 100%;
}
.ULdiv3 {
    position: relative;
    line-height: 150%;
    display: flex;
    align-items: center;
    width: 9.5rem;
    flex-shrink: 0;
}
.ULgroup-child {
    position: absolute;
    top: 0.06rem;
    right: 0;
    border-radius: 20px;
    background-color: #292d32;
    border: 1px solid #000;
    box-sizing: border-box;
    width: 5rem;
    height: 1.53rem;
}
.ULdiv4,
.ULrectangle-parent {
    width: 5rem;
    height: 1.53rem;
}
.ULdiv4 {
    position: absolute;
    top: 0;
    right: 0;
    line-height: 150%;
    font-weight: 300;
    display: flex;
    align-items: center;
    justify-content: center;
}
.ULrectangle-parent {
    position: relative;
    text-align: center;
    font-size: 0.75rem;
    color: #fff;
}
.ulistprofile,
.ulistprofile1 {
    display: flex;
    flex-direction: row;
}
.ulistprofile1 {
    align-self: stretch;
    border-radius: 8px;
    background-color: #fff;
    overflow: hidden;
    padding: 0.75rem 1rem;
    align-items: center;
    justify-content: center;
    gap: 1.63rem;
}
.ulistprofile {
    position: absolute;
    top: 0;
    left: calc(50% - 180px);
    width: 22.5rem;
    align-items: flex-start;
    justify-content: flex-start;
}
.ulist,
.ulist1 {
    position: relative;
    width: 100%;
    right: 0;
    left: 0;
    height: 5.25rem;
}
.UDMoveLink{
    cursor: pointer;
}
.ulist {
    /* top: 9.31rem; */
}
.ulist1 {
    top: 14.56rem;
}
.alluserlist,
.adminuserlist {
    position: absolute;
}
.alluserlist {
    background-color: #f8f5f0;
    width: 100%;
    height: 100%;
    text-align: left;
    font-size: 1rem;
    color: #000;
    font-family: Inter;
}
.usersearch,
.ULsearch {
    position: absolute;
    overflow: hidden;
}
.usersearch {
    width: calc(100% - 25px);
    top: 7rem;
    margin-left: 0.8rem;
    max-width: 100%;
    height: 2.31rem;
}
.ULsearch {
    top: 7rem;
    right: 0.9rem;
    font-size: 1rem;
    letter-spacing: 0.15px;
    display: flex;
    align-items: center;
    justify-content: center;
    height: 37px;
    width: 40px;
}
.adminuserlist{
    top: 14rem;
    width: 100%;
    right: 0;
    left: 0;
    height: auto;
}
