@import url("https://fonts.googleapis.com/css2?family=Zen+Dots:wght@400&display=swap");
@import url("https://fonts.googleapis.com/css2?family=Roboto:wght@500&display=swap");
@import url("https://fonts.googleapis.com/css2?family=Inter:wght@400&display=swap");
body {
    margin: 0;
    line-height: normal;
}
.header{
    width: 100%;
}
.btn1_wrapper {
    display: flex;
    align-items: center;
    justify-content: center;

}

.JElogo-ex-child {
    height: 100%;
    width: 100%;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    background-color: transparent;
}
.JEdiv,
.JElogo-ex-child,
.JEregister {
    position: absolute;
}
.JEdiv {
    top: -2.85%;
    left: 0;
    letter-spacing: -0.01em;
    background: linear-gradient(202.17deg, #03c75a, #85c644);
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
}

.JEicon-notification,
.JEicon-profile-circle {
    height: 41.77%;
    width: 3.73%;
    top: 38.89%;
    right: 6.36%;
    bottom: 19.34%;
    left: 89.91%;
}
.JEicon-profile-circle {
    height: 41.67%;
    width: 4.59%;
    right: 0;
    bottom: 19.44%;
    left: 95.41%;
}
.JEregister-parent {
    position: absolute;
    height: 42.86%;
    width: 90.83%;
    top: 28.57%;
    right: 4.44%;
    bottom: 28.57%;
    left: 4.72%;
}
.JElogo-ex {
    position: absolute;
    top: 0;
    left: 0;
    width: 20.63rem;
    height: 5.13rem;
}
.JEsearch-icon {
    position: relative;
    border-radius: 40px;
    width: 2.5rem;
    height: 2.5rem;
}
.JEactions,
.JEtitle,
.JEtitle1 {
    position: absolute;
}
.JEactions {
    right: 0.5rem;
    bottom: 0.5rem;
    overflow: hidden;
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: flex-start;
    margin-bottom: 0.25rem;
}
.JEtitle {
    width: 50%;
    bottom: 1.06rem;
    left: 1.25rem;
    letter-spacing: 0.15px;
    font-weight: 300;
    border: none;
    font-size: 60%;
    outline: none;
}
.JEtitle1 {
    width: 20%;
    height: 65%;
    bottom: 0.5rem;
    right: 6rem;
    letter-spacing: 0.15px;
    font-weight: 300;
    /* border: none; */
    font-size: 80%;
}
.JEapp-bar-top {
    position: absolute;
    height: 100%;
    width: 100%;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    background-color: #fff;
}
.JEyangdo-bar {
    align-self: stretch;
    position: relative;
    box-shadow: 0 0 2px rgba(0, 0, 0, 0.12), 0 1px 2px rgba(0, 0, 0, 0.24);
    height: 3.13rem;
}
.JEdiv1 {
    flex: 1;
    position: relative;
    line-height: 0.88rem;
    text-transform: uppercase;
}
.JEframe {
    align-self: stretch;
    background-color: #ebebf0;
    border: 1px solid #ebebf0;
    display: flex;
    padding: 0.63rem 0.5rem;
    align-items: center;
    justify-content: center;
}
.JEframe,
.JEframe1,
.JEsegmented-control1 {
    flex: 1;
    overflow: hidden;
    flex-direction: row;
}
.JEframe1 {
    align-self: stretch;
    border: 1px solid #ebebf0;
    display: flex;
    padding: 0.63rem 0.5rem;
    align-items: center;
    justify-content: center;
}
.JEsegmented-control1 {
    border-radius: 5px;
    background-color: #fff;
    height: 2.06rem;
    align-items: flex-start;
}
.JEjtap,
.JEsegmented-control,
.JEsegmented-control1 {
    display: flex;
    justify-content: flex-start;
}
.JEsegmented-control {
    align-self: stretch;
    flex-direction: row;
    align-items: flex-start;
    text-align: center;
    font-size: 0.88rem;
    color: #000;
    font-family: Inter;
}
.JEjtap {
    position: absolute;
    top: 5.19rem;
    left: 0;
    width: 100%;
    height: 5.19rem;
    flex-direction: column;
    align-items: center;
    gap: 0.13rem;
    font-size: 1.25rem;
    font-family: Roboto;
}
.JEjoinheader {
    align-self: stretch;
    position: relative;
    height: 10.38rem;
}
.JEinstance-child,
.JEjlist-inner {
    position: absolute;
    width: 100%;
    top: 0;
    right: 0;
    left: 0;
}
.JEinstance-child {
    height: 100%;
    bottom: 0;
    border-radius: 6px;
    background-color: rgba(0, 0, 0, 0.03);
    box-shadow: 0 4px 4px rgba(0, 0, 0, 0.25);
}
.JEjlist-inner {
    height: 8.38rem;
    cursor: pointer;
}
.JLp2 {
    position: absolute;
    top: 0rem;
    right: 3rem;
    width: 7.06rem;
    height: 1.13rem;
    color: #ed4c5c;
