}
.JEp1 {
    margin: 5px;
}
.JEspan1 {
    font-size: 0.8rem;
    line-height: 0.75rem;
}
.JEdiv4 {
    position: absolute;
    width: 100%;
    top: 1.06rem;
    left: 5.88rem;
    color: #000;
    display: inline-block;
}
.JEemoji-flag-in-hole {
    position: absolute;
    top: 0.06rem;
    right: 6.06rem;
    width: 1rem;
    height: 1.03rem;
}
.JEdiv5 {
    position: absolute;
    top: 0;
    right: 0;
    line-height: 150%;
    display: inline-block;
    width: 5.5rem;
}
