    width: 1.5rem;
    height: 1.5rem;
}
.JEavatar-user-60,
.JErectangle1 {
    top: 2.5rem;
    left: -0.94rem;
    border-radius: 90px;
    width: 5.63rem;
    height: 5.63rem;
}
.JEavatar-user-60 {
    top: 1.13rem;
    left: 0.63rem;
    border-radius: 10px;
    background-color: #fff;
    width: 3.75rem;
    height: 3.75rem;
    overflow: hidden;
}
.JEgroup-child {
    top: 0;
    left: 0;
    border-radius: 20px;
    background-color: rgba(217, 217, 217, 0.16);
    border: 1px solid #ed4c5c;
    box-sizing: border-box;
    height: 1.25rem;
}
.JEd-16,
.JEgroup-child,
.JErectangle-parent {
    position: absolute;
    width: 3.75rem;
}
.JEd-16 {
    top: 0.07rem;
    left: 0;
    line-height: 150%;
    display: flex;
    align-items: center;
    justify-content: center;
    height: 1.18rem;
}
.JErectangle-parent {
    top: 6.13rem;
    left: 0.63rem;
    height: 1.25rem;
    text-align: center;
}
.JEjlist-child {
    position: absolute;
    width: calc(100% - 25.08px);
    top: 5.47rem;
    right: 0.78rem;
    left: 0.78rem;
    border-top: 1px solid #fff;
    box-sizing: border-box;
    height: 0.06rem;
}
.JEjlist {
    position: relative;
    width: 100%;
    height: 8.38rem;
    margin-bottom: 15px;
}
.JEjlist-wrapper {   
    
}
.JEjlist-parent,
.JEjoinlistreact {
    display: flex;
    align-items: flex-start;
    justify-content: flex-start;
}
.JEjlist-parent {
    align-self: stretch;
    flex-direction: row;
    flex-wrap: wrap;
    gap: 1.75rem;
    font-size: 0.75rem;
    color: #ed4c5c;
    font-family: Inter;
}
.JEjoinlistreact {
    position: relative;
    flex-direction: column;
    padding: 0.94rem;
    box-sizing: border-box;
    gap: 0.63rem;
    text-align: left;
    font-size: 1.88rem;
    color: rgba(0, 0, 0, 0.87);
    font-family: "Zen Dots";
}
.nav-style {
    color: black;
}

.JLbtn {
    font-size: 0.9rem;
